import React, {useState} from "react";
import "./form.css"

function Form(){
  const [firstName,setFirstName]=useState("");
  const[lastName,setLastName]=useState("");
  const [email,setEmail]=useState("");
  const [phone,setPhone]=useState("");
  const [age,setAge]=useState("");
  const [gender,setGender]=useState("");
  const [dob,setDob]=useState("");

  function show(_id) {
    var x = document.getElementById(_id);
      x.style.display = "block";
  }
  function hide(_id){
    var x =document.getElementById(_id);
      x.style.display="none"
  }
  const onSubmit = (e)=>{
    e.preventDefault();
    show("table");
    hide("form");
   } 
   
  return(
    <div>
    <form className="Form" onSubmit={onSubmit} id="form">
      <label className="Label">First Name :</label><br/>
      <input className="input" type="text"
             value={firstName}
             onChange={(e)=>{setFirstName(e.target.value)}}/>
      <br/>
      <label>Last Name :</label><br/>
      <input type="text"
              value={lastName}
              onChange={(e)=>{setLastName(e.target.value)}} />
      <br/>
      <label>Email :</label><br/>
      <input type ="email"
             value={email}
             onChange={(e)=>{setEmail(e.target.value)}} />
      <br/>
      <label>Phone Number :</label><br/>
      <input  type="tel"
              pattern="[0-9]{10}"
              value={phone}
              onChange={(e)=>{setPhone(e.target.value)}} />
      <br/>
      <label>Age :</label><br/>
      <input type="number"
              min="0"
              value={age}
              onChange={(e)=>{setAge(e.target.value)}} />
      <br/>
      <label>Gender</label><br/>
      <select class="btn btn-secondary dropdown-toggle" type="button" id="dropdownMenuButton1" type ="text" value={gender} onChange={(e)=>{setGender(e.target.value)}}>
        <option default>Gender</option>
        <option>Male</option>
        <option>Female</option>
        <option>Other</option>
      </select>
      <br/>
      <label>Date Of Birth</label><br/>
      <input type="date" 
             name="birthdate"
             value={dob} 
             onChange={(e)=>{setDob(e.target.value)}} />
      <br/>
      <button class="btn btn-primary" type="submit">Submit Form</button>
    </form>
    <br/>
    <table class="table" id="table">
      <tr>
        <td class="td">First Name</td>
        <td class="td">{firstName}</td>
      </tr>
      <tr>
        <td class="td">Last Name</td>
        <td class="td">{lastName}</td>
      </tr>
      <tr>
        <td class="td">Email</td>
        <td class="td">{email}</td>
      </tr>
      <tr>
        <td class="td">Phone number</td>
        <td class="td">{phone}</td>
      </tr>
      <tr>
        <td class="td">Age</td>
        <td class="td">{age}</td>
      </tr>
      <tr>
        <td class="td">Gender</td>
        <td class="td">{gender}</td>
      </tr>
      <tr>
        <td class="td">Date of Birth</td>
        <td class="td">{dob}</td>
      </tr>
    </table>
    </div>
    
  )
}
export default Form;